export default{
	data(){
		return{
			vipAmount:"",
			vipMaskNum:"",
			showWd:-1,
			paying:false,
		}
	},
	methods:{
		async payVip(){
			if(this.paying){
				return
			}
			this.paying = true;
			uni.showLoading({
				title:"请求中...",
				mask:true
			})
			var that = this
			await this.$http({
				apiName:"becomVip",
				method:"POST",
			}).then(res => {
				let obj = {
					nonceStr: res.nonceStr,
					timeStamp: res.timeStamp,
					package:res.packageValue,
					signType:res.signType,
					paySign:res.paySign,
					appId: res.appId,
				}
				
				uni.requestPayment({
				    provider: 'wxpay',
				    ...obj,
				    success: function (res) {
						uni.hideLoading()
						that.paying = false;
						that.$store.commit("setIsVip",1);
						uni.showToast({
							title: "支付成功",
							duration: 1500,
						})
						setTimeout(()=>{
							// uni.navigateBack()
							uni.redirectTo({
								url:'getVipGift'
							})
						},1500)
				    },
				    fail: function (err) {
						uni.hideLoading()
						that.paying = false;
				        uni.showToast({
							title: "支付失败",
							duration: 1500,
							icon:"none"
				       })
				    }
				});
			}).catch(_ => {
				uni.hideLoading();
				this.paying = false;
			})
			
		},
		toPath(path){
			uni.navigateTo({
				url:path
			})
		}
	},
	onLoad(opt) {
		this.vipAmount = opt.vipAmount;
		this.vipMaskNum = opt.vipMaskNum;
		this.showWd = opt.showWd;
	}
}
